.App {
  text-align: center;
  width: 100%;
  height: 100vh;
  background-color: lightcyan;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  
}
.app_container {
  width: 25%;
  height: 80vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
}
h1 {
  color: white;
  background: transparent;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
input {
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  font-size: 20px;
  background: transparent;
  width: 60%;
  font-weight: 500;
  border-bottom: 2px solid #8566aa;
  outline: none;

}

button {
  min-height: 40px;
  width: 40px;
  border-radius: 50px;
  background-color: #8566aa;
  border-color: transparent;
  color: #fff;
  font-size: 40px;
  outline: none;
  border: none;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}
button:hover {
  background-color: #20bf6b;
}
ol {
  margin-top: 30px;
}
ol li {
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
}

.todo_style {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}

.delete {
  width: 40px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: crimson;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3)
}
.delete:hover {
  color: white;
}

@media (max-width: 768px) {
  .app_container {
    width: 55%;
  }
  .delete {
    display: flex;
    margin: 0 15px 0 15px;
    justify-content: center;
    align-items: center;
  }
  button {
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    font-size: 30px;
    color: #fff
  }
}